.content-inner-wrapper {
  padding: 0 40px 40px 40px;
}
@media screen and (max-width: 738px) {
  .content-inner-wrapper {
    padding: 0 20px 20px 20px ;
  }
}
.content-inner-wrapper .sider-wrapper {
  border-right: 1px solid #ebedf0;
  height: calc(100vh - 64px - 40px);
  text-align: center;
  color: rgba(0, 0, 0, 0.65);
  overflow-y: auto;
}
.content-inner-wrapper .sider-wrapper .sider-avatar {
  width: 132px;
  height: 132px;
  border-radius: 50%;
}
.content-inner-wrapper .sider-wrapper .name {
  font-size: 1.5em;
  font-weight: 700;
  margin-top: 10px;
}
.content-inner-wrapper .sider-wrapper .title {
  font-size: 13px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.content-inner-wrapper .sider-wrapper .link-list {
  width: 160px;
  line-height: 24px;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}
.content-inner-wrapper .sider-wrapper .link-list a {
  vertical-align: top;
  padding-left: 5px;
  color: inherit;
}
.content-inner-wrapper .sider-wrapper .link-list li {
  padding: 0 4px;
  cursor: pointer;
}
.content-inner-wrapper .sider-wrapper .link-list li:hover {
  border-radius: 4px;
  background: #eee;
}
.content-inner-wrapper .sider-wrapper .show-list {
  text-align: left;
}
.content-inner-wrapper .sider-wrapper .show-list li {
  padding: 0 20px;
}
.content-inner-wrapper .sider-wrapper .show-list li a {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 14px;
  line-height: 32px;
  color: #8590a6;
  cursor: pointer;
}
.content-inner-wrapper .sider-wrapper .show-list li:hover {
  background: #f0f2f5;
}
.content-inner-wrapper .sider-wrapper .show-list li:hover a {
  color: #40a9ff;
}
.content-inner-wrapper .sider-wrapper .tags-content {
  padding: 0 20px;
  overflow: hidden;
}
.content-inner-wrapper .sider-wrapper .tags-content a {
  color: inherit;
}
